body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.left {
  float: left;
  font-size: 32px;
  font-weight: 500;
}

.ant-menu-item-selected {
  background-color: inherit !important;
}

.right {
  float: right;
  font-size: 24px;
  margin-right: 15px;
}
.trigger {
  font-size: 30px;
  line-height: 60px;
  padding: 0 20px;
  cursor: pointer;
  transition: color 0.3s;
}

.trigger:hover {
  color: #1890ff;
}

@media only screen and (max-width: 600px) {

  .right {
    float: right;
    font-size: 14px;
    margin-right: 7px;
  }

  .left {
    float: left;
    font-size: 18px;
    font-weight: 500;
  }

  .header {
    padding: 0 !important;
  }
  
}
.newbutton{
  background-color: inherit;
  border: none;
  text-decoration: underline;
  padding: 0px 0px;
  padding-left: 7px;
  color: blue;
  text-align: center;
  display: inline-block;
  font-size: 16px;
}

